import './App.css';
import { HashRouter, Route, Routes, BrowserRouter } from "react-router-dom";
import FirstName from "./pages/Firstname"
import LastName from "./pages/Lastname"
import Email from "./pages/Email"
import Phone from "./pages/Phone"
import Review from "./pages/Review"
import Confirmation from "./pages/Confirmation"


function App() {
  return (
    <div>
      <HashRouter baseurl="/react-redux-form/">
        <Routes>
        <Route path='/react-redux-form/' exact element={<FirstName />} />
        <Route path='/react-redux-form/firstname' exact element={<FirstName />} />
        <Route path='/react-redux-form/lastname' exact element={<LastName />} />
        <Route path='/react-redux-form/email' exact element={<Email />} />
        <Route path='/react-redux-form/phone' exact element={<Phone />} />
        <Route path='/react-redux-form/review' exact element={<Review />} />
        <Route path='/react-redux-form/confirmation' element={<Confirmation />} />
        </Routes>
      </HashRouter>
    </div>
  );
}

export default App;